.dashboard {
    width: 100%;
    padding: 5px;
    display: flex;
    flex-flow: column nowrap;
    align-items: stretch;
    align-content: stretch;
    justify-content: center;
    background-color: #f5f5f5;
    gap: 50px;
}
.item-dashboard {
    background-color: antiquewhite;
    cursor: pointer;
    width: 300px;
}
.dashboard-container {
    display: flex;
}
